import { Component } from "@angular/core";
import { IonicPage, NavController } from "ionic-angular";
import { MeetingDetailAgendaPage } from "../meeting-agenda/meeting-detail-agenda";

@Component({
    selector: "meeting-detail",
    templateUrl: "meeting-detail.html"
  })
@IonicPage()
export class MeetingDetailPage {

    static TAG:string = 'MeetingDetailPage'
    static KEY_MODEL:string = 'model'

    items:Array<any> = [
        {  
            "is_deleted":0,
            "id":842,
            "nama":"Regresi UAT SMARt",
            "nomor_tiket":"R20182",
            "jenis_rapat":1,
            "status":0,
            "naskah_undangan":null,
            "tanggal_awal":"2018-12-04T19:53:11.000+0700",
            "created_by":"198604122007101001",
            "created_date":"2018-12-04T19:53:11.036+0700",
            "updated_by":null,
            "updated_date":null,
            "internal":true,
            "status_name":"DRAFT"
         },
         {  
            "is_deleted":0,
            "id":822,
            "nama":"Project SMART 2018 513",
            "nomor_tiket":"R20181",
            "jenis_rapat":1,
            "status":0,
            "naskah_undangan":null,
            "tanggal_awal":"2018-12-04T14:14:56.000+0700",
            "created_by":"198604122007101001",
            "created_date":"2018-12-04T14:14:56.333+0700",
            "updated_by":null,
            "updated_date":null,
            "internal":true,
            "status_name":"DRAFT"
         }
    ]

    constructor(private navCtrl:NavController) {

    }

    private getItems(): Array<any> {
        return this.items
    }

    private isItemExist(): boolean {
        return this.getItems().length > 0
    }

    private detail(model): void {
        var data = {}
        data[MeetingDetailAgendaPage.KEY_MODEL] = model
        this.navCtrl.push(MeetingDetailAgendaPage.TAG, data)
    }

}


